    <div v-else-if="lang===`en`">
    <h4 class="fw-bold">Passer-by, tell Poland ...</h4>
    <p class="fw-bold">
      Raymond Graham Swing, a radio announcer extremely popular in the United States, announced on the afternoon broadcast of May 18, 1944 for the whole of America: "The magnificent flanking maneuver that caused the capture of Cassino was successful thanks to the attack of the Polish troops, the greatest soldiers of the present war."
    </p>
    <p>
      Undoubtedly, the day of planting a white and red flag on the ruins of the Benedictine abbey in Monte Cassino was a great triumph for Polish soldiers. They were distinguished from all the armies fighting for this seemingly impregnable German redoubt, barring the Allies' way to Rome. Indeed, from the beginning of 1944, the best allied forces were bled out here one by one. In the first attack in January, over 2,000 American and French soldiers were killed in the swamps of the Rapido River. The British started the second attack in mid-February. When they bled out on Hill 593, they were replaced by Indians and just as heavily decimated. New Zealanders, including the Maori famous for their bravery, attacked the Liri Valley, and their attack was unsuccessful. After heavy fights at Monte Cassino, the New Zealand corps was disbanded ... In the third, March attack, the town of Cassino was turned into rubble, and the brave Gurkhas were thrown away from the ruins of the monastery - it was incredible! As Melchior Wańkowicz wrote, the famous Indian 4th Infantry Division, which lost 3,000 soldiers in this attack, for the first time in its history did not achieve the goal of the attack. German parachute shooters fully confirmed that they deserved to be called Hitler's military elite. The Allies called them the Green Devils. Impatient and nervous, Winston Churchill wrote to General Harold Alexander, Commander of the 15th Army Group in Italy: “I would like you to explain to me why the passage to Cassino, Mount Monastic, etc. only two or three miles wide is the only place you must hit. Five or six divisions have worn out when they climb into this mouth ... It is very difficult to understand why the most defended place is the only way forward ... " Alexander replied in a long message, explaining as precisely as possible that there was no other way to Rome. He emphasized the very difficult terrain and weather conditions that had been troubling the Allies since last autumn. Finally, he mentioned that his troops must struggle with selected enemy troops. The prime minister replied briefly and sarcastically: "Thank you very much for such a complete explanation ... It is certainly very difficult for the enemy."

    </p>
    <h4 class="fw-bold">2 The Corps goes into action</h4>
    <p>
      After the failure of the third Allied attack on Monte Cassino in the second half of March 1944, the front stagnated. It became clear to the Allies that fresh meals were needed to break the Gustav Line. It is an hour for the 2nd Polish Corps of General Władysław Anders, who entered Italy in December 1943 and became part of the British 8th Army. Churchill then wrote to General Alan Brooke, Chief of the Imperial Staff: "The Poles' entry into battle is now becoming an urgent matter." On March 24, 1944, General Oliver Leese, commander of the 8th Army, met with General Anders in Vinchiaturo. The commander of the 2nd Corps agreed that his soldiers should take part in the fourth, spring attack on the Gustav Line. As General Anders recalls: "Initially, the task assigned to the 2nd Polish Corps by the commander of the 8th Army was broadly defined:" get Monte Cassino and operate on Piedimonte ". Which meant nothing less than to break Gustav's line in its most vulnerable place and hit the strongest redoubt in Hitler's line. General Anders detailed this plan: “I envisioned first of all to break the defensive position above Monte Cassino, farther north, in order to obtain first the complete seclusion of the monastery, which was to be conquered only in the next phase. The subject of the first attack was the main bastions of the defense system: Hill 593 and Colle San Angelo. The mastery of Colle San Angelo was to result in the cloister's complete isolation. " In addition to the 2nd Polish Corps, the British 13th Corps was appointed to attack. The British were to cross the Rapido River and capture the suburbs between Cassino and the Liri River, isolate Cassino from the west and, after opening road number 6, hit the Hitler line. It was predicted that if both corps got the job done, Gustav's line would eventually break. The Moroccans and Tunisians from the French Expeditionary Force, who had to surprise the parachute shooters with an attack in the Aurunci Mountains, had to help them. The retreating Germans were to be crossed by the American 6th Corps with a blow from the bridgehead at Anzio. In total, the allies gathered 21 divisions and 11 independent brigades against 14 divisions and three German brigades. The Allies also prevailed in armor and artillery, and their aviation completely dominated the airspace over the Monte Cassino massif. However, this advantage was still canceled out by the skillful defense of the German parachute and mountain shooters. Churchill was right when he wrote that "the enemy is also very difficult". But as one of the officers of the elite 1st Parachute Rifle Division, Colonel Rudolf Böhmler, recalled: “The paratroopers knew well what was at stake. To keep Cassino under all circumstances was a fight to death. This time, however, an equally determined opponent was going to fight them: none of the nations fighting at Monte Cassino had with the Germans such scores to settle as the Poles.
    </p>
    <h4 class="fw-bold">Blood flows from the mountains</h4>
    <p>
     Almost the whole of April took the transfer and installation of units designated for the fourth attack on the Monte Cassino massif. This gigantic operation was difficult not only for logistical and field reasons. As General Anders wrote: “The conditions of the troops in the defense and starting positions for the attack were very difficult. The entire frontier area was day and night under heavy harassing enemy artillery and mortar fire. The soldier was hidden from primitive folders or large stones. Digging into the rocks was out of the question. " On April 27, General Anders invited the commanders of the two main units of his corps to choose by lot the directions in which they were to attack the Germans. General Bronisław Duch, for his 3rd Carpathian Rifle Division, attacked the hills 593–569, Massa Albanetta and then the monastery. The 5th Kresowa Infantry Division of general Nikodem Sulik was to take over Widmo, San Angello and hills 575 and move to Hitler's line. The day of the "D", or attack, was set for May 11. After a dozen or so hot days, a little rain fell on May 11. Officers had the last briefings and the last details were checked. The orders of the commander-in-chief were read in the units. General Anders concluded his order to the soldiers with the words: "With faith in the righteousness of Divine Providence, we go forward with the holy slogan in our hearts: God, Honor and the Fatherland" .. The commander of the 8th Army, General Leese, ended his order with the words of Admiral Nelson from Trafalgar: "Let everyone do their duty." In the evening they all set off to do their duty: the English and Indians of the 13th Corps on the Rapido, Moroccan and Tunisian highlanders in the Aurunci Mountains, and the Americans on the Tyrrhenian coast. Finally, Polish battalions of the Carpathian and Borderlands began to attack. Already in the first moments of the attack, it turned out that the artillery strike that preceded it did not do much harm to the enemy. The paratroopers opened a murderous fire on the attackers from their bunkers, perfectly integrated into the terrain, masked with boulders and bushes. It was only possible to locate them when they began to blow in series of machine guns and erkaems. General Anders emphasized in his memoirs: “Total darkness of night and smoke. You can't see anything for a few steps. Even soldiers of the same team, in a march or attempting to march forward, falling on fire and jumping up again, after outbursts close to them or among them, lose communication, find each other with poverty, do not count their condition. From the very beginning, the commanders of battalions, companies, and platoons are killed or wounded, and the command is taken by alternates and deputy deputies. " Despite this, the battalions of the 1st Carpathian Rifle Brigade managed to break into hill 593 and approach the so-called The gorge, leading to Massa Albanetta, and start the fight for Hill 569. The 5th Vilnius Infantry Brigade, on the other hand, stormed the hill, named by the Americans during their first attack as Widma. It was a real "molehill" of bunkers, which Polish shooters had to conquer one by one, incurring huge losses. The Kresowiacy tried to make their way towards Hill 575, but they did not get far, as the German fire covered the entire valley. The troops attacking San Angelo were even worse. The decimated companies retreated to Hill 706. It soon turned out that it was easier to take enemy positions than to hold them. The attackers and defenders engaged in a fierce battle that reached an intensity never before seen in this battle. Registered Colonel Ludwik Domoń wrote in his report: “The stubbornness is so great that those who have not seen these conditions cannot understand [them]. For example, I report that Platoon Leader Gorgolewski from 18 L [wowski] B [atalion] S [trzelców], cut by an enemy machine gun, first shoots through his legs into the bunker, and then, shot a second time through his breasts, rises and shouts: "Colleagues, hit those motherfuckers »and only then after a shot by a sharpshooter is he hit in the head with a scream dying on his lips:« Long live Poland »”. Unfortunately, the dedication and insane courage of the Poles did not manage to defeat the German paratroopers this time. It was useless to send the Shermans to the Gorge - they were decimated by mines and fire from anti-tank guns. As General Anders emphasizes, "The immediate introduction of fresh forces into the attack was impossible due to the very small capacity of the front roads and paths and the lack of space in the small front area." A retreat had to be heard, but the first, unsuccessful attack was not entirely in vain. Due to the fact that the Germans had to engage in the fight with Polish soldiers to the maximum extent, the British from the 13th Corps managed to gain bridgeheads and break the leading positions of the "Gustav" line. Colonel Böhmler, who commanded the defense of Hill 593, wrote admiringly in the report: "Carpathian riflemen had a great fight ...". The attack and fight of May 11-14 cost Poles 205 dead, 380 missing and 1000 wounded. The number of the missing persons was decreasing with each passing day, as the soldiers returned from the areas of the recent fighting and even broke out of German captivity.
    </p>
    <h4 class="fw-bold">Red and white is announcing Victoria</h4>
    <p>
      Meanwhile, in the headquarters of the 2nd Corps, General Anders with his officers was already preparing another attack. They are scheduled for 7.00 on May 17. The basic plan and direction of the attack remained unchanged. Polish battalions launched an attack just after the firestorm of their own artillery. The Poles had it so much easier that they jumped over the enemy firewalls, which had already been identified during the first attack, which does not mean that they did not suffer any losses. The soldiers died not only from enemy fire, but also from mines and traps. However, the Carpathians and the Kresovians were unstoppable. On that day, they captured the Spectrum ridge (with the exception of its northern part), as well as Hill 593 and the Gorge. Again, the opponents engaged in a murderous fight, and it seemed that the scenario of the first attack could repeat itself. But the next day, General Anders launched battalions that bled on May 11, reinforced by a commando company and part of the 15th Poznań Uhlans Regiment. Eventually he also dispatched two battalions drawn from gunners, drivers, supplies, cooks, and other rear soldiers. The general recalled: “It was a moment of crisis of battle when both sides, exhausted, lie facing each other, seemingly incapable of effort. Then the stronger will wins, the one who will get the final blow… ”. On the morning of May 18, the Poles took to the battle. Around 6.00 am, the soldiers of the 6th Carpathian Rifle Battalion stormed Masseria Albaneta. At the same time, soldiers from the 5th Battalion were climbing up Hill 569, breaking through the defense of the German shield troops that remained after most of their forces retreated overnight. At 9.00 am, patrols of the 12th Podolian Lancers Regiment set off towards Góra Klasztorna, pulling out of the bunkers the few paratroopers who had not managed to withdraw or stayed with the wounded as paramedics. They were also the first to take possession of the monastery ruins, on which they hung a pennant in the colors of the 12th Regiment - amaranth and navy blue. Around 11.30 am, soldiers from the 3rd platoon of the 3rd company of the 5th battalion of the 3rd Carpathian Rifle Division, under the command of Lieutenant Adam Lorenz, placed a white and red flag in the monastery, "so that everyone would know and see from a distance who had captured the rubble on the top." General Leese soon arrived at General Anders' headquarters with congratulations. With his consent, General Anders ordered the British Union Jack to be placed next to the Polish banner, as a sign of brotherhood in arms with the soldiers of the 13th Corps and as a sign of affiliation of the 2nd Corps to the 8th Army. At that time, fierce fighting continued in the episode of the 5th Kresowa Infantry Division. It was not until the next day, May 19, that Hill 575 was cleared of the enemy. The Germans finally withdrew to Hitler's line. Congratulations were flowing to the staff of the 2nd Corps from everywhere. The newspapers emphasized the combat qualities of Polish soldiers. It seemed for a moment that the Polish cause could no longer be abandoned by the Western Allies. These were all hopes in vain. The 2nd Corps went to a further, equally difficult and bloody campaign, and the Americans and the British to their policy of concessions to Stalin. As a wipe of tears, the words of appreciation of General Alexander remained to the Polish soldiers: "If I had been given a choice between any soldiers I would like to have under my command, he would have chosen you Poles." The Battle of Monte Cassino cost the 2nd Polish Corps 924 dead, 2,930 wounded and 345 missing. In the place of their eternal rest at the stupas of the hill they conquered, there is an inscription: "Passers-by, tell Poland that we have fallen faithful in its service."


    </p>
    <h4 class="fw-bold">Bibliography</h4>
    <p>
      W. Anders, Bez ostatniego rozdziału. Wspomnienia z lat 1939 –1946, Warszawa 2007<br>
      Bitwa o Monte Cassino, praca zbiorowa, Rzym 1994<br>
      F. Majdalany, Cassino: Portrait of a Battle, London 1957<br>
      Z. Wawer, Monte Cassino, Warszawa 2019<br>
    </p>
    </div>



    <div v-else-if="lang===`it`">
    <h4 class="fw-bold">Passante, dì alla Polonia ...</h4>
    <p class="fw-bold">
      Raymond Graham Swing, un annunciatore radiofonico estremamente popolare negli Stati Uniti, annunciò nella trasmissione pomeridiana del 18 maggio 1944 per tutta l'America: "La magnifica manovra di fiancheggiamento che ha causato la cattura di Cassino ha avuto successo grazie all'attacco dei polacchi truppe, i più grandi soldati della guerra attuale ".
    </p>
    <p>
      Indubbiamente, il giorno in cui è stata piantata una bandiera bianca e rossa sulle rovine dell'abbazia benedettina di Monte Cassino è stato un grande trionfo per i soldati polacchi. Si distinguevano da tutti gli eserciti che combattevano per questa ridotta tedesca apparentemente inespugnabile, impedendo agli alleati di raggiungere Roma. In effetti, dall'inizio del 1944, le migliori forze alleate furono dissanguate qui una ad una. Nel primo attacco di gennaio, oltre 2.000 soldati americani e francesi sono stati uccisi nelle paludi del fiume Rapido. Gli inglesi hanno iniziato il secondo attacco a metà febbraio. Quando sanguinarono sulla collina 593, furono sostituiti dagli indiani e altrettanto pesantemente decimati. I neozelandesi, inclusi i Maori famosi per il loro coraggio, attaccarono la Valle del Liri e il loro attacco non ebbe successo. Dopo pesanti combattimenti a Monte Cassino, il corpo della Nuova Zelanda è stato sciolto ... Nel terzo attacco di marzo, la città di Cassino è stata ridotta in macerie, ei coraggiosi Gurkha sono stati gettati via dalle rovine del monastero - è stato incredibile! Come ha scritto Melchior Wańkowicz, la famosa 4a divisione di fanteria indiana, che ha perso 3.000 soldati in questo attacco, per la prima volta nella sua storia non ha raggiunto l'obiettivo dell'attacco. I tiratori di paracadutisti tedeschi hanno confermato pienamente che meritavano di essere chiamati l'élite militare di Hitler. Gli alleati li chiamavano i diavoli verdi. Impaziente e nervoso, Winston Churchill scrisse al generale Harold Alexander, comandante del 15 ° Gruppo d'armata in Italia: “Vorrei che mi spiegaste perché il passaggio a Cassino, Monte Monastico, ecc. Largo solo due o tre miglia è l'unico posto che devi colpire. Cinque o sei divisioni si sono consumate quando si arrampicano in questa bocca ... È molto difficile capire perché il luogo più difeso sia l'unica via d'uscita ... "Alexander rispose in un lungo messaggio, spiegando il più precisamente possibile che lì non c'era altra strada per Roma. Ha sottolineato il terreno molto difficile e le condizioni meteorologiche che avevano afflitto gli Alleati dallo scorso autunno. Infine, ha detto che le sue truppe devono lottare con truppe nemiche selezionate. Il primo ministro ha risposto brevemente e sarcasticamente: "Grazie molto per una spiegazione così completa ... È certamente molto difficile per il nemico. "
    </p>
    <h4 class="fw-bold">2 Il Corpo entra in azione</h4>
    <p>
      Dopo il fallimento del terzo attacco alleato a Monte Cassino nella seconda metà di marzo 1944, il fronte ristagnò. Divenne chiaro agli alleati che i pasti freschi erano necessari per rompere la linea Gustav. È un'ora per il 2 ° Corpo d'Armata Polacco del Generale Władysław Anders, entrato in Italia nel dicembre 1943 ed entrato a far parte dell'8 ° Armata britannica. Churchill scrisse quindi al generale Alan Brooke, capo dello stato maggiore imperiale: "L'entrata in battaglia dei polacchi sta diventando una questione urgente". Il 24 marzo 1944, il generale Oliver Leese, comandante dell'8a armata, si incontrò con il generale Anders a Vinchiaturo. Il comandante del 2 ° corpo ha accettato che i suoi soldati prendessero parte al quarto attacco primaverile sulla linea Gustav. Come ricorda il generale Anders: "Inizialmente, il compito assegnato al 2 ° Corpo d'Armata Polacco dal comandante dell'8a Armata era ampiamente definito:" prendi Monte Cassino e opera su Piedimonte ". posizionare e colpire la ridotta più forte nella linea di Hitler. Il generale Anders ha dettagliato questo piano: "Ho immaginato prima di tutto di rompere la posizione difensiva sopra Monte Cassino, più a nord, al fine di ottenere prima il completo isolamento del monastero, che doveva essere conquistato solo nella fase successiva. Oggetto del primo attacco furono i principali baluardi del sistema difensivo: Colle 593 e Colle San Angelo. La maestria di Colle San Angelo avrebbe comportato il completo isolamento del chiostro. "Oltre al 2 ° Corpo polacco, il 13 ° corpo britannico è stato nominato per attaccare. Gli inglesi dovevano attraversare il fiume Rapido e catturare la periferia tra Cassino e il fiume Liri, isolare Cassino da ovest e, dopo aver aperto la strada numero 6, colpire la linea Hitler. Si prevedeva che se entrambi i corpi avessero portato a termine il lavoro, la linea di Gustav alla fine si sarebbe interrotta. Ad aiutarli sono stati i marocchini ei tunisini del corpo di spedizione francese, che hanno dovuto sorprendere i tiratori con il paracadute con un attacco sui monti Aurunci. I tedeschi in ritirata dovevano essere attraversati dal 6 ° Corpo d'armata americano con un colpo dalla testa di ponte di Anzio. In totale, gli alleati riunirono 21 divisioni e 11 brigate indipendenti contro 14 divisioni e tre brigate tedesche. Gli Alleati prevalsero anche in armature e artiglieria, e la loro aviazione dominava completamente lo spazio aereo sul massiccio del Monte Cassino. Tuttavia, questo vantaggio è stato comunque annullato dall'abile difesa dei paracadutisti e dei tiratori da montagna tedeschi. Aveva ragione Churchill quando scriveva che "anche il nemico è molto difficile". Ma come ha ricordato uno degli ufficiali della prima divisione d'élite dei fucili da paracadutismo, il colonnello Rudolf Böhmler: “I paracadutisti sapevano bene qual era la posta in gioco. Tenere Cassino in tutte le circostanze era una lotta all'ultimo sangue. Questa volta, però, un avversario altrettanto determinato stava per combatterli: nessuna delle nazioni che combattevano a Montecassino aveva con i tedeschi conti da pareggiare come i polacchi.
    </p>
    <h4 class="fw-bold">Il sangue scorre dalle montagne</h4>
    <p>
    Quasi tutto il mese di aprile ha avuto luogo il trasferimento e l'installazione delle unità destinate al quarto attacco al massiccio del Monte Cassino. Questa gigantesca operazione è stata difficile non solo per ragioni logistiche e sul campo. Come ha scritto il generale Anders: “Le condizioni delle truppe in difesa e le posizioni di partenza per l'attacco erano molto difficili. L'intera area di frontiera era giorno e notte sotto il pesante fuoco di artiglieria nemica e colpi di mortaio. Il soldato era nascosto da cartelle primitive o grosse pietre. Scavare nelle rocce era fuori questione. "Il 27 aprile, il generale Anders invitò i comandanti delle due unità principali del suo corpo a scegliere a sorte le direzioni in cui dovevano attaccare i tedeschi. Il generale Bronisław Duch, per la sua 3a divisione di fucili dei Carpazi, attaccò le colline 593-569 , Massa Albanetta e poi il monastero. La 5a divisione di fanteria di Kresowa del generale Nikodem Sulik doveva prendere il controllo di Widmo, San Angello e le colline 575 e trasferirsi sulla linea di Hitler. Il giorno della "D", o attacco, era fissato per l'11 maggio . Dopo una dozzina di giorni caldi, l'11 maggio è caduta un po 'di pioggia. Gli ufficiali hanno avuto gli ultimi briefing e sono stati controllati gli ultimi dettagli. Gli ordini del comandante in capo sono stati letti nelle unità. Il generale Anders ha concluso il suo ordine a i soldati con le parole: "Con fede nella giustizia della Divina Provvidenza, andiamo avanti con il santo slogan nei nostri cuori: Dio, onore e patria" .. Il comandante dell'8a armata, il generale Leese, ha concluso il suo ordine con le parole dell'ammiraglio Nelson da Trafalgar: "Che tutti facciano il loro dovere". In serata sono partiti tutti per fare il loro dovere: gli inglesi e gli indiani del 13 ° corpo d'armata sul Rapido, gli altipiani marocchini e tunisini sui monti Aurunci e gli americani sulla costa tirrenica. Alla fine, i battaglioni polacchi dei Carpazi e delle Terre di confine iniziarono ad attaccare. Già nei primi momenti dell'attacco, si è scoperto che il colpo di artiglieria che lo ha preceduto non ha fatto molto male al nemico. I paracadutisti hanno aperto un fuoco micidiale sugli assalitori dai loro bunker, perfettamente integrati nel terreno, mascherati da massi e cespugli. È stato possibile localizzarli solo quando hanno iniziato a soffiare in serie di mitragliatrici ed erkaem. Il generale Anders ha sottolineato nelle sue memorie: “Oscurità totale della notte e fumo. Non puoi vedere nulla per pochi passaggi. Anche i soldati della stessa squadra, in marcia o che tentano di marciare in avanti, cadendo in fiamme e saltando di nuovo in piedi, dopo esplosioni vicino a loro o tra loro, perdono la comunicazione, si ritrovano con la povertà, non contano la loro condizione. Fin dall'inizio, i comandanti di battaglioni, compagnie e plotoni vengono uccisi o feriti e il comando è preso da supplenti e vice deputati. "Nonostante questo, i battaglioni della 1a Brigata Fucilieri dei Carpazi sono riusciti a irrompere nella collina 593 e ad avvicinarsi alla cosiddetta Gola, che porta a Massa Albanetta, e iniziare la lotta per il Colle 569. La 5a Brigata di fanteria di Vilnius, invece , ha preso d'assalto la collina, chiamata Widma dagli americani durante il loro primo attacco, un vero e proprio "cumulo" di bunker, che i tiratori polacchi hanno dovuto conquistare uno ad uno, subendo enormi perdite. I Kresowiacy cercarono di dirigersi verso la collina 575, ma non andarono lontano, poiché il fuoco tedesco coprì l'intera valle. Le truppe che attaccavano San Angelo erano anche peggio. Le compagnie decimate si ritirarono sulla collina 706. Ben presto si scoprì che era più facile prendere posizioni nemiche che mantenerle. Gli attaccanti e i difensori si sono impegnati in una feroce battaglia che ha raggiunto un'intensità mai vista prima in questa battaglia. Il colonnello registrato Ludwik Domoń ha scritto nel suo rapporto: “La testardaggine è così grande che coloro che non hanno visto queste condizioni non possono capirle. Ad esempio, riferisco che il comandante di plotone Gorgolewski da 18 L [wowski] B [atalion] S [trzelców], colpito da una mitragliatrice nemica, prima spara attraverso le sue gambe nel bunker e poi gli spara una seconda volta attraverso i seni , si alza e grida: "Colleghi, colpite quei figli di puttana" e solo allora dopo un colpo di un tiratore scelto viene colpito alla testa con un grido morente sulle labbra: «Viva la Polonia» ". Purtroppo la dedizione e il coraggio insano di Questa volta i polacchi non sono riusciti a sconfiggere i paracadutisti tedeschi. È stato inutile inviare gli Sherman alla gola - sono stati decimati dalle mine e dal fuoco dei cannoni anticarro. Come sottolinea il generale Anders, "L'introduzione immediata di nuove forze in l'attacco era impossibile a causa della capacità molto ridotta delle strade e dei sentieri anteriori e della mancanza di spazio nella piccola area del fronte. "Si doveva udire una ritirata, ma il primo attacco fallito non fu del tutto vano. A causa del fatto che i tedeschi dovettero impegnarsi nella lotta Soldati polacchi al massimo grado, gli inglesi del 13 ° Corpo riuscirono a guadagnare teste di ponte e rompere le posizioni di comando della linea "Gustav". Il colonnello Böhmler, che comandava la difesa della collina 593, ha scritto con ammirazione nel rapporto: "I fucilieri dei Carpazi hanno avuto una grande battaglia ...". L'attacco e il combattimento dell'11-14 maggio costarono ai polacchi 205 morti, 380 dispersi e 1000 feriti. Il numero delle persone scomparse diminuiva ogni giorno che passava, poiché i soldati tornavano dalle aree dei recenti combattimenti e riuscivano persino a fuggire dalla prigionia tedesca.


    </p>
    <h4 class="fw-bold">Il bianco e il rosso stanno annunciando Victoria</h4>
    <p>
      Nel frattempo, nel quartier generale del 2 ° Corpo, il generale Anders con i suoi ufficiali stava già preparando un altro attacco. Sono previste per le 7.00 del 17 maggio. Il piano di base e la direzione dell'attacco sono rimasti invariati. I battaglioni polacchi hanno lanciato un attacco subito dopo la tempesta di fuoco della propria artiglieria. Per i polacchi è stato molto più facile saltare i firewall nemici, che erano già stati identificati durante il primo attacco, il che non significa che non abbiano subito perdite. I soldati morirono non solo per il fuoco nemico, ma anche per mine e trappole. Tuttavia, i Carpazi e i Kresoviani erano inarrestabili. Quel giorno, hanno catturato la cresta dello spettro (ad eccezione della sua parte settentrionale), così come la collina 593 e la gola. Ancora una volta, gli avversari ingaggiarono un combattimento omicida, e sembrava che lo scenario del primo attacco potesse ripetersi. Ma il giorno successivo, il generale Anders lanciò battaglioni che sanguinarono l'11 maggio, rinforzati da una compagnia di commando e da parte del 15 ° reggimento degli ulani di Poznań. Alla fine inviò anche due battaglioni tratti da artiglieri, autisti, rifornimenti, cuochi e altri soldati di retroguardia. Il generale ha ricordato: “E 'stato un momento di crisi di battaglia quando entrambe le parti, esauste, giacciono l'una di fronte all'altra, apparentemente incapaci di sforzo. Allora vincerà il più forte, colui che prenderà il colpo finale… ”. La mattina del 18 maggio i polacchi presero parte alla battaglia. Intorno alle 6.00, i soldati del 6 ° Battaglione Fucilieri dei Carpazi hanno preso d'assalto la Masseria Albaneta. Allo stesso tempo, i soldati del 5 ° battaglione stavano salendo sulla collina 569, sfondando la difesa delle truppe di scudo tedesche che rimasero dopo che la maggior parte delle loro forze si ritirarono durante la notte. Alle 9.00 le pattuglie del 12 ° Reggimento dei Lancieri Podoliani sono partite verso Góra Klasztorna, tirando fuori dai bunker i pochi paracadutisti che non erano riusciti a ritirarsi o rimasti con i feriti come paramedici. Furono anche i primi a impossessarsi delle rovine del monastero, su cui appesero un gagliardetto nei colori del 12 ° reggimento: amaranto e blu marino. Intorno alle 11.30, i soldati del 3 ° plotone della 3a compagnia del 5 ° battaglione della 3a Divisione Fucilieri dei Carpazi, al comando del tenente Adam Lorenz, hanno posto una bandiera bianca e rossa nel monastero, "in modo che tutti potessero sapere e vedere da lontano che aveva catturato le macerie in cima ". Il generale Leese arrivò presto al quartier generale del generale Anders con le congratulazioni. Con il suo consenso, il generale Anders ordinò che la British Union Jack fosse collocata accanto allo stendardo polacco, in segno di fratellanza in armi con i soldati del 13 ° Corpo e come segno di affiliazione del 2 ° Corpo all'8 ° Armata. A quel tempo, i feroci combattimenti continuarono nell'episodio della 5a divisione di fanteria di Kresowa. Fu solo il giorno successivo, 19 maggio, che la collina 575 fu ripulita dal nemico. I tedeschi alla fine si ritirarono sulla linea di Hitler. Congratulazioni giunsero al personale del 2 ° Corpo da ogni parte. I giornali hanno sottolineato le qualità di combattimento dei soldati polacchi. Per un momento sembrò che la causa polacca non potesse più essere abbandonata dagli alleati occidentali. Erano tutte speranze vane. Il 2 ° Corpo è andato a una campagna ulteriore, altrettanto difficile e sanguinosa, e gli americani e gli inglesi alla loro politica di concessioni a Stalin. Come asciugare le lacrime, ai soldati polacchi rimasero le parole di apprezzamento del generale Alexander: "Se mi fosse stata data una scelta tra i soldati che avrei voluto avere sotto il mio comando, avrebbe scelto voi polacchi". La battaglia di Montecassino costò al 2 ° Corpo d'Armata Polacco 924 morti, 2.930 feriti e 345 dispersi. Al posto del loro eterno riposo presso gli stupa della collina che hanno conquistato, c'è un'iscrizione: "Passanti, dite alla Polonia che siamo caduti fedeli al suo servizio".


    </p>
